import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { FaBars, FaTimes } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { Button } from './Button';

import './navbar.css';

export class Navbar extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            menuClick: false,
            loggedIn: true,
            button: true,
        }
    }

    handleClick() { this.setState({ menuClick: !this.state.menuClick }); }
    closeMobileMenu = () => { this.setState({ menuClick: false }); }
    showButton() { 
        if(window.innerWidth >= 960) {

        }
        this.setState({ button: window.innerWidth >= 960 ? true : false }); 
    }

    dashboard = () => {
        return this.state.loggedIn ? (<li className='nav-item'>
            <Link to='/dashboard' className='nav-links' onClick={ () => this.closeMobileMenu() }>Dashboard</Link>
        </li>) : null
    }
    
    static eventListeners() {
        window.addEventListener('resize', this.showButton);
        this.showButton();
    }

    render() {

        return (
            <>
                <div className="numbers">
                    <p id="width"></p>
                    <p id="height"></p>
                    <p id="calls"></p>
                </div>
                <div className="navbar">
                    <div className="navbar-container container">
                        <Link to="/" className="navbar-logo">
                            Commfee
                        </Link>
                        <div className="menu-icon" onClick={ () => this.handleClick() }>
                            { this.state.menuClick ? <FaTimes /> : <FaBars /> }
                        </div>
                        <ul className={ this.state.menuClick ? 'nav-menu active' : 'nav-menu' }>
                            <li className='nav-item'>
                                <Link to='/' className='nav-links' onClick={ () => this.closeMobileMenu() }>
                                    Home
                                </Link>
                            </li>
                            { this.dashboard() }
                            <li className='nav-item'>
                                <Link to={ this.state.loggedIn ? '/profile' : '/login'} className='nav-links' onClick={ () => this.closeMobileMenu() }>
                                    { this.state.loggedIn ? 'Profile' : 'Log In / Register' }
                                </Link>
                            </li>
                            <li className='nav-item'>
                                <Link to='/notifications' className='nav-links' onClick={ () => this.closeMobileMenu() }>Notifications</Link>
                            </li>
                            <li className="nav-btn">
                                { this.state.button ? 
                                    (<Link to='/register' className="btn-link">
                                        <Button buttonStyle="btn--outline">REGISTER</Button>
                                    </Link>) : 
                                    (<Link to='/register' className="btn-link">
                                        <Button buttonStyle="btn--outline" buttonSize="btn--mobile">REGISTER</Button>
                                    </Link>)
                                }
                            </li>
                        </ul>
                    </div>
                </div>
            </>
        );
    }

}