import React from 'react';
import { Link } from 'react-router-dom';

import './Button.css';

const STYLES = ['btn--primary', 'btn--outline'];

const SIZES = ['btn--medium', 'btn--large', 'btn--mobile', 'btn--wide'];

const COLOR = ['primary', 'blue', 'red', 'green'];

export const Button = (props) => {

    const checkButtonStyle = STYLES.includes(props.buttonStyle) ? props.buttonStyle : STYLES[0];
    const checkButtonSize = SIZES.includes(props.buttonSize) ? props.buttonSize : SIZES[0];
    const checkButtonColor = COLOR.includes(props.buttonColor) ? props.buttonColor : null;

    return (
        <Link to='/login' className='btn-mobile'>
            <button className={`btn ${checkButtonStyle} ${checkButtonSize} ${checkButtonColor}`} onClick={ props.onClick } type={ props.type }>
                { props.children }
            </button>
        </Link>
    );
    
}